/**
 * Ê∂àÈô§ÁÆ°ÁêÜÂô®
 * Ë¥üË¥£Â§ÑÁêÜÊñπÂùóÁöÑËøûÈÄöÂå∫ÂüüÊ£ÄÊµãÂíåÊ∂àÈô§ÈÄªËæë
 */

import { BlockData } from './BoardManager';

export class EliminationManager {
    
    /**
     * ÂàùÂßãÂåñÊ∂àÈô§ÁÆ°ÁêÜÂô®
     */
    init() {
        console.log('‚úÖ Ê∂àÈô§ÁÆ°ÁêÜÂô®ÂàùÂßãÂåñÂÆåÊàê');
    }
    
    /**
     * Êü•Êâæ‰ªéÊåáÂÆö‰ΩçÁΩÆÂºÄÂßãÁöÑËøûÈÄöÂêåËâ≤ÊñπÂùó
     * ‰ΩøÁî®ÂπøÂ∫¶‰ºòÂÖàÊêúÁ¥¢(BFS)ÁÆóÊ≥ï
     */
    findConnectedBlocks(boardData: BlockData[][], startRow: number, startCol: number): {row: number, col: number}[] {
        console.log(`\nüîç ===== BFSËøûÈÄöÊêúÁ¥¢ÂºÄÂßã =====`);
        console.log(`üéØ Ëµ∑Âßã‰ΩçÁΩÆ: (${startRow}, ${startCol})`);
        
        const boardSize = boardData.length;
        console.log(`üìè Ê£ãÁõòÂ§ßÂ∞è: ${boardSize}x${boardSize}`);
        
        // Ê£ÄÊü•Ëµ∑Âßã‰ΩçÁΩÆ
        if (!this.isValidPosition(startRow, startCol, boardSize)) {
            console.log(`‚ùå Ëµ∑Âßã‰ΩçÁΩÆÊó†ÊïàÔºåË∂ÖÂá∫Ê£ãÁõòËåÉÂõ¥`);
            return [];
        }
        
        if (boardData[startRow][startCol].type === -1) {
            console.log(`‚ùå Ëµ∑Âßã‰ΩçÁΩÆÊòØÁ©∫‰ΩçÔºåÁ±ªÂûã‰∏∫ -1`);
            return [];
        }
        
        const targetType = boardData[startRow][startCol].type;
        console.log(`üé® ÁõÆÊ†áÊñπÂùóÁ±ªÂûã: ${targetType}`);
        
        const visited: boolean[][] = [];
        const connectedBlocks: {row: number, col: number}[] = [];
        
        // ÂàùÂßãÂåñËÆøÈóÆÊ†áËÆ∞
        for (let i = 0; i < boardSize; i++) {
            visited[i] = new Array(boardSize).fill(false);
        }
        console.log(`‚úÖ ËÆøÈóÆÊ†áËÆ∞Êï∞ÁªÑÂàùÂßãÂåñÂÆåÊàê`);
        
        // BFSÈòüÂàó
        const queue: {row: number, col: number}[] = [{row: startRow, col: startCol}];
        visited[startRow][startCol] = true;
        connectedBlocks.push({row: startRow, col: startCol});
        console.log(`üöÄ BFSÈòüÂàóÂàùÂßãÂåñÔºåËµ∑ÂßãÊñπÂùóÂ∑≤Âä†ÂÖ•`);
        
        // Âõõ‰∏™ÊñπÂêëÔºö‰∏ä„ÄÅ‰∏ã„ÄÅÂ∑¶„ÄÅÂè≥
        const directions = [
            {dr: -1, dc: 0},  // ‰∏ä
            {dr: 1, dc: 0},   // ‰∏ã
            {dr: 0, dc: -1},  // Â∑¶
            {dr: 0, dc: 1}    // Âè≥
        ];
        
        let step = 0;
        while (queue.length > 0) {
            step++;
            const current = queue.shift()!;
            console.log(`üîÑ Ê≠•È™§ ${step}: Â§ÑÁêÜÊñπÂùó (${current.row}, ${current.col}), ÈòüÂàóÂâ©‰Ωô: ${queue.length}`);
            
            // Ê£ÄÊü•Âõõ‰∏™ÊñπÂêëÁöÑÈÇªÊé•ÊñπÂùó
            for (let i = 0; i < directions.length; i++) {
                const dir = directions[i];
                const dirName = ['‰∏ä', '‰∏ã', 'Â∑¶', 'Âè≥'][i];
                const newRow = current.row + dir.dr;
                const newCol = current.col + dir.dc;
                
                console.log(`  üëÄ Ê£ÄÊü•${dirName}ÊñπÂêë: (${newRow}, ${newCol})`);
                
                // Ê£ÄÊü•‰ΩçÁΩÆÊúâÊïàÊÄß
                if (!this.isValidPosition(newRow, newCol, boardSize)) {
                    console.log(`    ‚ùå ‰ΩçÁΩÆÊó†ÊïàÔºåË∂ÖÂá∫ËæπÁïå`);
                    continue;
                }
                
                // Ê£ÄÊü•ÊòØÂê¶Â∑≤ËÆøÈóÆ
                if (visited[newRow][newCol]) {
                    console.log(`    ‚ùå Â∑≤ËÆøÈóÆËøá`);
                    continue;
                }
                
                const neighborType = boardData[newRow][newCol].type;
                console.log(`    üé® ÈÇªÂ±ÖÊñπÂùóÁ±ªÂûã: ${neighborType} (ÁõÆÊ†á: ${targetType})`);
                
                // Ê£ÄÊü•ÊòØÂê¶‰∏∫Áõ∏ÂêåÁ±ªÂûãÁöÑÊñπÂùó
                if (neighborType === targetType) {
                    visited[newRow][newCol] = true;
                    queue.push({row: newRow, col: newCol});
                    connectedBlocks.push({row: newRow, col: newCol});
                    console.log(`    ‚úÖ ÂåπÈÖçÔºÅÂä†ÂÖ•ËøûÈÄöÂå∫ÂüüÔºåÂΩìÂâçÊÄªÊï∞: ${connectedBlocks.length}`);
                } else {
                    console.log(`    ‚ùå Á±ªÂûã‰∏çÂåπÈÖç`);
                }
            }
        }
        
        console.log(`üîç BFSÊêúÁ¥¢ÂÆåÊàêÔºÅÊâæÂà∞ ${connectedBlocks.length} ‰∏™ËøûÈÄöÁöÑÁ±ªÂûã ${targetType} ÊñπÂùó:`);
        connectedBlocks.forEach((block, index) => {
            console.log(`  ${index + 1}. (${block.row}, ${block.col})`);
        });
        console.log(`üîç ===== BFSËøûÈÄöÊêúÁ¥¢ÁªìÊùü =====\n`);
        
        return connectedBlocks;
    }
    
    /**
     * Ê£ÄÊü•‰ΩçÁΩÆÊòØÂê¶ÊúâÊïà
     */
    private isValidPosition(row: number, col: number, boardSize: number): boolean {
        return row >= 0 && row < boardSize && col >= 0 && col < boardSize;
    }
    
    /**
     * Ê£ÄÊü•ÊòØÂê¶ËøòÊúâÂèØÊ∂àÈô§ÁöÑËøûÈÄöÂå∫ÂüüÔºà2‰∏™Êàñ‰ª•‰∏äÁõ∏ËøûÁöÑÂêåËâ≤ÊñπÂùóÔºâ
     */
    hasValidMoves(boardData: BlockData[][]): boolean {
        const boardSize = boardData.length;
        const visited: boolean[][] = [];
        
        // ÂàùÂßãÂåñËÆøÈóÆÊ†áËÆ∞
        for (let i = 0; i < boardSize; i++) {
            visited[i] = new Array(boardSize).fill(false);
        }
        
        // ÈÅçÂéÜÊï¥‰∏™Ê£ãÁõò
        for (let row = 0; row < boardSize; row++) {
            for (let col = 0; col < boardSize; col++) {
                // Ë∑≥ËøáÁ©∫‰ΩçÂíåÂ∑≤ËÆøÈóÆÁöÑ‰ΩçÁΩÆ
                if (boardData[row][col].type === -1 || visited[row][col]) {
                    continue;
                }
                
                // Êü•ÊâæËøûÈÄöÂå∫Âüü
                const connectedBlocks = this.findConnectedBlocksWithVisited(
                    boardData, row, col, visited
                );
                
                // Â¶ÇÊûúÊâæÂà∞2‰∏™Êàñ‰ª•‰∏äÁöÑËøûÈÄöÊñπÂùóÔºåËØ¥ÊòéËøòÊúâÊúâÊïàÁßªÂä®
                if (connectedBlocks.length >= 2) {
                    console.log(`‚úÖ ÊâæÂà∞ÊúâÊïàÁßªÂä®: ${connectedBlocks.length} ‰∏™ËøûÈÄöÊñπÂùó`);
                    return true;
                }
            }
        }
        
        console.log('‚ùå Ê≤°ÊúâÊâæÂà∞ÊúâÊïàÁßªÂä®');
        return false;
    }
    
    /**
     * Êü•ÊâæËøûÈÄöÊñπÂùóÔºàÂ∏¶ËÆøÈóÆÊ†áËÆ∞ÁöÑÁâàÊú¨ÔºåÁî®‰∫éhasValidMovesÔºâ
     */
    private findConnectedBlocksWithVisited(
        boardData: BlockData[][], 
        startRow: number, 
        startCol: number,
        globalVisited: boolean[][]
    ): {row: number, col: number}[] {
        const boardSize = boardData.length;
        
        if (!this.isValidPosition(startRow, startCol, boardSize) || 
            boardData[startRow][startCol].type === -1 ||
            globalVisited[startRow][startCol]) {
            return [];
        }
        
        const targetType = boardData[startRow][startCol].type;
        const connectedBlocks: {row: number, col: number}[] = [];
        const queue: {row: number, col: number}[] = [{row: startRow, col: startCol}];
        
        globalVisited[startRow][startCol] = true;
        connectedBlocks.push({row: startRow, col: startCol});
        
        const directions = [
            {dr: -1, dc: 0},  // ‰∏ä
            {dr: 1, dc: 0},   // ‰∏ã
            {dr: 0, dc: -1},  // Â∑¶
            {dr: 0, dc: 1}    // Âè≥
        ];
        
        while (queue.length > 0) {
            const current = queue.shift()!;
            
            for (const dir of directions) {
                const newRow = current.row + dir.dr;
                const newCol = current.col + dir.dc;
                
                if (!this.isValidPosition(newRow, newCol, boardSize) ||
                    globalVisited[newRow][newCol] ||
                    boardData[newRow][newCol].type !== targetType) {
                    continue;
                }
                
                globalVisited[newRow][newCol] = true;
                queue.push({row: newRow, col: newCol});
                connectedBlocks.push({row: newRow, col: newCol});
            }
        }
        
        return connectedBlocks;
    }
    
    /**
     * È´ò‰∫ÆÊòæÁ§∫ËøûÈÄöÂå∫ÂüüÔºàÁî®‰∫éÈ¢ÑËßàÔºâ
     */
    highlightConnectedBlocks(
        boardData: BlockData[][], 
        startRow: number, 
        startCol: number,
        highlightCallback: (row: number, col: number, highlight: boolean) => void
    ): {row: number, col: number}[] {
        const connectedBlocks = this.findConnectedBlocks(boardData, startRow, startCol);
        
        // Âè™Êúâ2‰∏™Êàñ‰ª•‰∏äÁöÑÊñπÂùóÊâçËøõË°åÈ´ò‰∫Æ
        if (connectedBlocks.length >= 2) {
            connectedBlocks.forEach(({row, col}) => {
                highlightCallback(row, col, true);
            });
        }
        
        return connectedBlocks;
    }
    
    /**
     * Ê∏ÖÈô§È´ò‰∫ÆÊòæÁ§∫
     */
    clearHighlight(
        blocks: {row: number, col: number}[],
        highlightCallback: (row: number, col: number, highlight: boolean) => void
    ) {
        blocks.forEach(({row, col}) => {
            highlightCallback(row, col, false);
        });
    }
    
    /**
     * Ëé∑ÂèñÊ£ãÁõò‰∏äÊâÄÊúâËøûÈÄöÂå∫ÂüüÁöÑÁªüËÆ°‰ø°ÊÅØ
     */
    getConnectedRegionsStats(boardData: BlockData[][]): {
        totalRegions: number,
        validRegions: number,
        largestRegionSize: number,
        blockTypeDistribution: {[type: number]: number}
    } {
        const boardSize = boardData.length;
        const visited: boolean[][] = [];
        
        // ÂàùÂßãÂåñËÆøÈóÆÊ†áËÆ∞
        for (let i = 0; i < boardSize; i++) {
            visited[i] = new Array(boardSize).fill(false);
        }
        
        let totalRegions = 0;
        let validRegions = 0;
        let largestRegionSize = 0;
        const blockTypeDistribution: {[type: number]: number} = {};
        
        // ÈÅçÂéÜÊï¥‰∏™Ê£ãÁõò
        for (let row = 0; row < boardSize; row++) {
            for (let col = 0; col < boardSize; col++) {
                // Ë∑≥ËøáÁ©∫‰ΩçÂíåÂ∑≤ËÆøÈóÆÁöÑ‰ΩçÁΩÆ
                if (boardData[row][col].type === -1 || visited[row][col]) {
                    continue;
                }
                
                const blockType = boardData[row][col].type;
                
                // Êü•ÊâæËøûÈÄöÂå∫Âüü
                const connectedBlocks = this.findConnectedBlocksWithVisited(
                    boardData, row, col, visited
                );
                
                if (connectedBlocks.length > 0) {
                    totalRegions++;
                    
                    if (connectedBlocks.length >= 2) {
                        validRegions++;
                    }
                    
                    largestRegionSize = Math.max(largestRegionSize, connectedBlocks.length);
                    
                    // ÁªüËÆ°ÊñπÂùóÁ±ªÂûãÂàÜÂ∏É
                    if (!blockTypeDistribution[blockType]) {
                        blockTypeDistribution[blockType] = 0;
                    }
                    blockTypeDistribution[blockType] += connectedBlocks.length;
                }
            }
        }
        
        return {
            totalRegions,
            validRegions,
            largestRegionSize,
            blockTypeDistribution
        };
    }
    
    /**
     * Ë∞ÉËØïÔºöÊâìÂç∞ËøûÈÄöÂå∫Âüü‰ø°ÊÅØ
     */
    debugPrintConnectedRegions(boardData: BlockData[][]) {
        const stats = this.getConnectedRegionsStats(boardData);
        
        console.log('üìä ËøûÈÄöÂå∫ÂüüÁªüËÆ°:');
        console.log(`  ÊÄªÂå∫ÂüüÊï∞: ${stats.totalRegions}`);
        console.log(`  ÂèØÊ∂àÈô§Âå∫ÂüüÊï∞: ${stats.validRegions}`);
        console.log(`  ÊúÄÂ§ßÂå∫ÂüüÂ§ßÂ∞è: ${stats.largestRegionSize}`);
        console.log(`  ÊñπÂùóÁ±ªÂûãÂàÜÂ∏É:`, stats.blockTypeDistribution);
    }
}
